<template>
	<div class="login">
		<div class="container">
			<form @submit.prevent="userLogin">
				<div v-show="messageErr" class="alert alert-danger" role="alert">
					Invaild Email / Password
				</div>
				<div class="mb-3 flex">
					<label for="exampleInputEmail1" class="form-label"
						>Email address</label
					>
					<input
						type="email"
						class="form-control"
						id="exampleInputEmail1"
						aria-describedby="emailHelp"
						placeholder="Enter Your Email"
						v-model="user.email"
					/>
				</div>
				<div class="mb-3">
					<label for="exampleInputPassword1" class="form-label">Password</label>
					<input
						type="password"
						class="form-control"
						id="exampleInputPassword1"
						placeholder="Enter Your Password"
						v-model="user.password"
					/>
				</div>
				<div class="footer-form">
					<router-link to="/signup">Create New Account ?</router-link>

					<button @click="timer" type="submit" class="btn btn-primary">
						Login
					</button>
				</div>
				<div class="rest-pass">
					<router-link to="/reset-password" v-show="showForget"
						>Forget Your Password ?</router-link
					>
				</div>
			</form>
		</div>
	</div>
</template>

<script>
import firebase from "firebase";
export default {
	name: "LogIn",
	data() {
		return {
			user: {
				email: "",
				password: "",
			},
			messageErr: "",
			timeLogin: 0,
			showForget: "",
		};
	},
	watch: {
		timeLogin: function () {
			this.timeLogin === 3 ? (this.showForget = "3") : "";
		},
	},
	methods: {
		userLogin() {
			firebase
				.auth()
				.signInWithEmailAndPassword(this.user.email, this.user.password)
				.then(() => {
					window.localStorage.setItem("login", this.user.email);
					this.timeLogin = 0;
					this.showForget = "";
					this.$router.push("/");
				})
				.catch((err) => {
					this.messageErr = err;
				});
		},
		timer() {
			this.timeLogin++;
		},
	},
};
</script>

<style lang="scss" scoped>
.container {
	display: flex;
	justify-content: center;
	align-items: center;
	height: calc(100vh - 100px);
	text-align: start;

	form {
		align-items: flex-start;

		input {
			width: 300px;
		}

		.footer-form {
			width: 100%;
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		a {
			display: inline-block;
			text-decoration: none;
			font-size: 15px;
			padding-left: 10px;
			color: #212529;
			transition: 0.3s linear;
		}
		a:hover {
			color: #3bb983;
		}
		button {
			display: block;
			background-color: #3bb983;
			border: none;
			transition: 0.3s linear;
		}
		button:hover {
			background-color: #212529;
			color: #3bb983;
		}
		.rest-pass {
			a {
				color: red;
			}
			a:hover {
				color: red;
			}
		}
	}
}
</style>
